import React, {Component} from 'react'
import {Icon} from 'antd'

class Register extends Component{
    constructor(){
        super();
        this.state={
            gender:''
        };
    }

    render(){
        return(
            <div id={'register'}>
                <div className={'avatarAndTip'}>
                    <div className={'avatar'} ref={avatar=>this.avatar=avatar}>
                        <input ref={'avatarInp'} className={'avatarInp'} type={'file'} onChange={()=>{
                            this.avatar.style.backgroundImage='url('+URL.createObjectURL(this.refs.avatarInp.files[0])+')';
                        }}/>
                    </div>
                    <span className={'tip'} onClick={()=>{
                        this.refs.avatarInp.click();
                    }}></span>
                </div>
                <span className={'accountAndPass'}>
                    <p className={'accountLead'}></p>
                    <input ref={'account'} className={'account'} type={'email'} placeholder={''} maxLength={40}/>
                    <p className={'passLead'}></p>
                    <input ref={'password'} className={'password'} type={'password'} placeholder={'6-20'} maxLength={20}/>
                </span>
                <span className={'lead'}>
                    <br/>
                    <br/>
                    <br/>
                    
                </span>
                <br/>
                <span className={'nicknameContainer'}>
                    <span className={'nicknameLead'}></span>
                    <input ref={'nickname'} className={'nickname'} maxLength={15} type={'text'}/>
                </span>
                <div className={'genderContainer'}>
                    <span className={'genderLead'}></span>
                    <select className={'gender'} onChange={(e)=>{
                        this.setState({
                            gender:e.target.value
                        });
                    }}>
                        <option value={''}></option>
                        <option value={''}></option>
                        <option value={''}></option>
                    </select>
                    <span className={'genderDisplay'}>{this.state.gender}</span>
                    <span className={'downIcon'} ><Icon type="caret-down" /></span>
                </div>
                <span className={'birthdayLead'}></span>
                <input ref={'birthday'} type={'date'} placeholder={''} className={'birthday'}/>
                <br/>
                <span className={'signLead'}></span>
                <br/>
                <textarea ref={'sign'} className={'sign'} maxLength={50}/>
                <br/>
                <span  className={'nameLead'}></span>
                <input ref={'name'} placeholder={''} className={'name'} type={'text'} maxLength={20}/>
                <span className={'phoneLead'}></span>
                <input ref={'phone'} placeholder={''} className={'phone'} type={'text'} maxLength={11}/>
                <br/>
                <span className={'IDLead'}></span>
                <input ref={'ID'} placeholder={''} className={'ID'} type={'text'} maxLength={18}/>
                <br/>
                <button className={'submit'} onClick={()=>{
                    let refs=this.refs;
                    let ving=this.props.container.ving;
                    let account=refs.account.value;
                    let password=refs.password.value;
                    let reg_ac=/^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/;
                    let reg_pass=/^[0-9a-zA-Z_]{6,20}$/;
                    if (!reg_ac.test(account)){
                        ving.message('');
                    }else if (!reg_pass.test(password)){
                        ving.message('6-20');
                    }else {
                        let userInfo={
                            birth:refs.birthday.value,
                            gender:this.state.gender,
                            id:account,
                            idNumber:refs.ID.value,
                            name:refs.nickname.value,
                            passwd:password,
                            phone:refs.phone.value,
                            realName:refs.name.value,
                            signature:refs.sign.value
                        };

                        let xmlHttp=new XMLHttpRequest();
                        xmlHttp.open('post',ving.url.register,true);
                        xmlHttp.setRequestHeader('Content-Type', 'application/json');
                        xmlHttp.send(JSON.stringify(userInfo));
                        xmlHttp.onreadystatechange=()=>{
                            if (xmlHttp.readyState!==4){}
                            else if (xmlHttp.status!==200)
                                ving.message('');
                            else {
                                let data=xmlHttp.responseText;
                                if (data===''){
                                    ving.message('');
                                }
                                else if (data==='Already exist') {
                                    ving.message('');
                                }
                                else {
                                    let avatar=new FormData();
                                    avatar.append('uId',account);
                                    avatar.append('portrait',refs.avatarInp.files[0]);
                                    let avatarXML=new XMLHttpRequest();
                                    avatarXML.open('post',ving.url.setUserAvatar);
                                    avatarXML.send(avatar);
                                    avatarXML.onreadystatechange=()=>{
                                        if (avatarXML.readyState!==4){}
                                        else if (avatarXML.status!==200)
                                            ving.message('');
                                        else{
                                            if (avatarXML.responseText==='false'){
                                                ving.message('');
                                            }
                                            ving.login(data,password);
                                        }
                                    };

                                    ving.message('(/)/');
                                }
                            }
                        };
                    }

                }}></button>
            </div>
        );
    }
}

export default Register;


