import React, {Component} from 'react'
import {Icon} from 'antd'
import '../css/CreateC.css'

class CreateC extends Component {
    constructor(props) {
        super(props);
        this.ving = props.ving;
        this.state = {
            added:null,
            my:null
        };
        this.circle = [];
        this.music = '';
    }

    componentDidMount() {
        let ving = this.ving;
        let navigate = ving.navigate;
        navigate.style.left = '5%';

        ving.setState({
            vingOrReturn: '< ',
            Ving: '',
            separator: '',
            first: '',
            second: '',
            third: '',
            fourth: '',
            return: () => {
                ving.returnLast();
            },
        });

        this.last = 0;
        let self = this;
        this.circle.forEach((item,index)=>{
            item.onclick=()=>{
                self.circle[self.last].style.border = '';
                item.style.border = '0.1rem solid #70e8f5';
                self.last = index;
            }
        });
        this.circle[0].onclick();

        //
        let isIE = navigator.userAgent.match(/MSIE (\d)/i);
        isIE = isIE ? isIE[1] : undefined;
        let isFF = /FireFox/i.test(navigator.userAgent);
        let my = this.my;
        let added = this.added;
        if (isIE < 9) { //MouseWheel
            added.attachEvent("onmousewheel", function (e) {
                //
                //340120
                added.scrollTop += e.wheelDelta / 2;
                //
                return false;
            });
            my.attachEvent("onmousewheel", function (e) {
                //
                //340120
                my.scrollTop += e.wheelDelta / 2;
                //
                return false;
            });
        }
        else if (!isFF) { //MouseWheel
            added.addEventListener("mousewheel", function (e) {
                //
                added.scrollTop += -e.wheelDelta / 2;
                //
                e.preventDefault();
            }, false);
            my.addEventListener("mousewheel", function (e) {
                //
                my.scrollTop += -e.wheelDelta / 2;
                //
                e.preventDefault();
            }, false);
        }
        else { //DOMMouseScroll
            added.addEventListener("DOMMouseScroll", function (e) {
                //
                //3
                added.scrollTop += e.detail * 80;
                //
                e.preventDefault();
            }, false);
            my.addEventListener("DOMMouseScroll", function (e) {
                //
                //3
                my.scrollTop += e.detail * 80;
                //
                e.preventDefault();
            }, false);
        }
        ving.touchMove(added);
        ving.touchMove(my);


        let xmlHttp=new XMLHttpRequest();
        xmlHttp.open('get',ving.url.personalPros+this.ving.user.uId);
        xmlHttp.send();
        xmlHttp.onreadystatechange=()=>{
            if (xmlHttp.readyState!==4){}
            else if (xmlHttp.status!==200)
                ving.message(''+xmlHttp.status);
            else {
                if (xmlHttp.responseText === '')
                    return;
                let products=JSON.parse(xmlHttp.responseText);
                let display=[];
                products.forEach((item,index)=>{
                    display[index]=
                        <span className={'myOne'} key={item.pId}>
                            <img src={item.cover} alt={''} className={'oneImg'}/>
                            <span className={'oneName'}>{item.pName}</span>
                            <span className={'oneOperation'} onClick={()=>{
                                self.add(item,index);
                            }}>+</span>
                        </span>
                });
                self.setState({
                    added:[],
                    my:display
                });
            }
        };
    }
    add(item,index){
        let added=this.state.added;
        added[index]=
            <span className={'one'} key={item.pId} pname={item.pName}>
                <img src={item.cover} alt={''} className={'oneImg'}/>
                <span className={'oneName'}>{item.pName}</span>
                <span className={'oneOperation'} onClick={()=>{
                    this.delete(item,index);
                }}>-</span>
            </span>;
        let my=this.state.my;
        my[index]=null;
        this.setState({
            added:added,
            my:my
        });
    }
    delete(item,index){
        let my=this.state.my;
        my[index]=
            <span className={'myOne'} key={item.pId}>
                <img src={item.cover} alt={''} className={'oneImg'}/>
                <span className={'oneName'}>{item.pName}</span>
                <span className={'oneOperation'} onClick={()=>{
                    this.add(item,index);
                    }}>+</span>
            </span>;
        let added=this.state.added;
        added[index]=null;
        this.setState({
            added:added,
            my:my
        });
    }

    render() {
        return (
            <div id={'createC'}>
                <div className={'left'}>
                    <div className={'cover'}>
                        <span className={'character'}>+</span>
                        <p className={'coverTip'}></p>
                        <input ref={cover => this.cover = cover} type={'file'} className={'coverFile'} onChange={() => {
                            let div = this.cover.parentNode;
                            let span = div.getElementsByClassName('character')[0];
                            let p = div.getElementsByClassName('coverTip')[0];
                            let image = div.getElementsByClassName('coverImg')[0];
                            span.style.opacity = 0;
                            p.style.opacity = 0;
                            if (image !== undefined) {
                                image.src = URL.createObjectURL(this.cover.files[0]);
                            } else {
                                let img = new Image();
                                img.src = URL.createObjectURL(this.cover.files[0]);
                                img.alt = '';
                                img.className = 'coverImg';
                                div.appendChild(img);
                            }
                            div.style.border = '0px';
                        }}/>
                    </div>
                    <div ref={life => this.circle[0] = life} className={'life'}></div>
                    <div ref={dubing => this.circle[3] = dubing} className={'dubbing'}></div>
                    <br/>
                    <div ref={reading => this.circle[2] = reading} className={'reading'}></div>
                    <div ref={ASMR => this.circle[4] = ASMR} className={'ASMR'}>ASMR</div>
                    <br/>
                    <div ref={oralSkill => this.circle[1] = oralSkill} className={'oralSkill'}></div>
                    <br/>
                    <span className={'visibilityLead'}></span><br/>
                    <div className={'visibility'} onMouseOver={
                        () => {
                            this.select.style.opacity = 1;
                        }
                    } onMouseOut={
                        () => {
                            this.select.style.opacity = 0;
                        }
                    }><Icon type="down" className={'down'}/></div>
                    <span className={'selection'} ref={select => this.select = select} onMouseOver={
                        () => {
                            this.select.style.opacity = 1;
                            this.select.style.backgroundColor = 'grey';
                        }
                    } onMouseOut={
                        () => {
                            this.select.style.backgroundColor = '#343a40';
                            this.select.style.opacity = 0;
                        }
                    } onClick={
                        () => {
                            let last = this.select.innerText;
                            let icon = this.select.previousSibling.getElementsByClassName("down")[0];
                            this.select.innerHTML = this.select.previousSibling.innerText;
                            this.select.previousSibling.innerText = last;
                            this.select.previousSibling.appendChild(icon);
                        }
                    }></span>
                </div>


                <div className={'right'}>
                    <div className={'productions'}>
                        <span className={'addedLead'}></span>
                        <span className={'myLead'}></span>
                        <br/>
                        <div className={'added'} ref={added => this.added = added}>
                            {this.state.added}
                        </div>

                        <span className={'barBack'}> </span>

                        <div className={'my'} ref={my => this.my = my}>
                            {this.state.my}
                        </div>

                        <span className={'barBack'}> </span>

                    </div>
                    <div className={'otherInfo'}>
                        <span className={'titleLead'}></span>
                        <input ref={title => this.title = title} type={'text'} className={'title'}/>
                        <br/>
                        <span className={'labelLead'}></span>
                        <input ref={label => this.label = label} type={'text'} className={'label'}
                               placeholder={'";"'}/>
                        <br/>
                        <span className={'descriptionLead'}></span>
                        <br/>
                        <textarea ref={description => this.description = description} className={'description'}
                                  maxLength={'100'} placeholder={''}/>
                        <button className={'finish'} onClick={() => {
                            let plist=[];
                            this.state.added.forEach((item,index)=>{
                                plist[index]={
                                    pId:item.key,
                                    pName:item.props.pname
                                };
                            });

                            let album={
                                aName: this.title.value,
                                creatorId: this.ving.user.uId,
                                creatorName: this.ving.user.uName,
                                description: this.description.value,
                                label: this.label.value,
                                productionNumber:plist.length,
                                visible:this.select.innerText === '' ? 1 + '' : 0 + '',
                            };
                            let info={
                                plist:plist,
                                album:album
                            };


                            let self = this;

                            self.ving.message('...o(**)o', 5000);
                            let infoXml = new XMLHttpRequest();
                            infoXml.open('post', this.ving.url.createAlbumInfo, true);
                            infoXml.setRequestHeader('Content-Type', 'application/json');
                            infoXml.send(JSON.stringify(info));
                            infoXml.onreadystatechange = () => {
                                if (infoXml.readyState !== 4) {
                                }
                                else if (infoXml.status !== 200)
                                    self.ving.message('' + infoXml.status);
                                else {
                                    let file = new FormData();
                                    file.append('aId', infoXml.responseText);
                                    file.append('cover', this.cover.files[0]);

                                    let fileXml = new XMLHttpRequest();
                                    fileXml.open('post', self.ving.url.setAlbumCover, true);
                                    fileXml.send(file);
                                    fileXml.onreadystatechange = () => {
                                        if (fileXml.readyState !== 4) {}
                                        else if (fileXml.status !== 200)
                                            self.ving.message('' + fileXml.status);
                                        else {
                                            self.ving.toNext(0);
                                            self.ving.user.albumNumber++;
                                        }
                                    }
                                }
                            };
                        }}><br/>
                        </button>
                    </div>
                </div>
            </div>
        );
    }
}

export default CreateC;


